<template>
  <div class="chooseaddressWrap">
      <header>
          <router-link to="/msite"><i class="el-icon-arrow-left"></i> </router-link>
          <div class="top_title"><span>选择地址</span></div>
      </header>
      <ul class="deliverable_address">
          <li v-for="(a,index) in addressMsg" :key="index" @click="chooseThis(index)">
              <i :class="active == index ? 'choosed_address default_address el-icon-success' : ''"></i>
              <div>
                  <div class="header1">
                      <span>{{a.name}}</span>
                      <span>先生</span>
                      <span>{{a.phone}}</span>
                  </div>
                  <div class="address_detail">
                      <span>公司</span>
                      <p class="ellipsis">{{a.address}}</p>
                  </div>
              </div>
          </li>
      </ul>
      <router-link to="" class="add_icon_footer">
          <i class="el-icon-circle-plus-outline"></i>
          <span>新增地址</span>
      </router-link>
  </div>
</template>

<script>
export default {
name:'ChooseAddress',
data() {
    return {
        addressMsg:'',
        active:123,
        userid:''
    }
},
created(){
    this.userid = localStorage.getItem('userId')
    // this.addressMsg = JSON.parse(localStorage.getItem("addressMsg"));
    this.axios.get("/v1/users/"+this.userid+"/addresses",{
            params:{
                user_id:this.userid,
            }
        }).then(res=>{
            console.log(res)
            this.addressMsg = res;
        }).catch(err=>{})
},
methods:{
    chooseThis(i){
        this.active = i;
        this.$router.push('/confirmorder');
        localStorage.setItem("thisAddress",i)
    }
}
}
</script>

<style scoped>
    .chooseaddressWrap{
        overflow: hidden;
    }
header{
    background-color: #3190e8;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 1.95rem;
}
a{
    text-decoration: none;
}
header>a>i{
    left: .4rem;
    width: .6rem;
    height: 1rem;
    color: #fff;
    line-height: 2.2rem;
    margin-left: .4rem;
}
.top_title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
}
.top_title>span{
    font-size: .8rem;
    color: #fff;
    text-align: center;
    font-weight: 700;
}
.deliverable_address{
    background-color: #fff;
    margin-top: 1.95rem;
}
.deliverable_address li {
    display: flex;
    align-items: center;
    border-bottom: .025rem solid #f5f5f5;
    padding: .7rem;
    line-height: 1rem;
    overflow: hidden;
}
.deliverable_address li .default_address {
    opacity: 1;
}
.deliverable_address li .choosed_address {
    display: block;
    width: .8rem;
    height: .8rem;
    color: #4cd964;
    margin-right: .4rem;
}
.header1{
    font-size: .7rem;
    color: #333;
}
.header span:first-of-type {
    font-size: .8rem;
    font-weight: 700;
}
.address_detail {
    width: 100%;
    display: flex;
    align-items: center;
}
.add_icon_footer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2.5rem;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 204;
}
.address_detail p {
    font-size: .6rem;
    color: #777;
    width: 40%;
}
.address_detail span {
    font-size: .5rem;
    color: #fff;
    border-radius: .15rem;
    background-color: #ff5722;
    height: .6rem;
    line-height: .6rem;
    padding: 0 .2rem;
    margin-right: .3rem;
}
.add_icon_footer span{
    font-size: .7rem;
    color: #3190e8;
    margin-left: .3rem;
}
</style>